<template>
  <v-ons-splitter>
      <v-ons-splitter-side swipeable collapse width="250px"
        :animation="$ons.platform.isAndroid() ? 'overlay' : 'reveal'"
        :open.sync="menuIsOpen">
        <menu-page></menu-page>
      </v-ons-splitter-side>

      <v-ons-splitter-content>
        <v-ons-page @toggleMenu="menuIsOpen = !menuIsOpen">
          <v-ons-toolbar class="home-toolbar">
            <div class="left">
              <v-ons-toolbar-button @click="$emit('toggleMenu')">
                <v-ons-icon icon="ion-navicon, material:md-menu"></v-ons-icon>
              </v-ons-toolbar-button>
            </div>
            <div class="center">{{ msg }}</div>
          </v-ons-toolbar>

          <div class="guide-text"> Connect your Projector to Mobile </div>
          <lottie-view width="min(100vw, 100vh)" height="min(100vw, 100vh)" path="static/yoga-developer.json" />

          <v-ons-button class="guide-ok" modifier="large" @click="$emit('replace-page', canvasPage)">Ok, I connected.</v-ons-button>

        </v-ons-page>
      </v-ons-splitter-content>
    </v-ons-splitter>
</template>

<script>
import LottieView from './LottieView.vue'
import MenuPage from './MenuPage.vue'
import CanvasPage from './CanvasPage.vue'

export default {
  name: 'home',
  data () {
    return {
      menuIsOpen: false,
      msg: 'This is LightWarp!',
      canvasPage: CanvasPage
    }
  },
  methods: {
    goTo (url) {
      window.open(url, '_blank')
    }
  },
  components: {
    MenuPage,
    CanvasPage,
    LottieView
  }
}
</script>

<style scoped>
.header {
  text-align: center;
}

img {
  max-width: 300px;
}

ons-list-title {
  text-transform: none;
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px;
}

ons-card {
  text-align: center;
}

ons-list-item, ons-card {
  cursor: pointer;
}

.guide-text {
  text-align: center;
  font-size: min(15vw, 15vh);
}

.guide-ok {
  width: 13em;
  bottom: 5em;
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
}
</style>



