import * as PIXI from 'pixi.js'
import './Pixi.Ex.js'

var pixiApp = null
var rect = null
var width = 100
var height = 100

var startSize = null
var startPosition = null
var data = null

function refreshRect () {
  const rectPolygons = [
    {x: 0, y: 0},
    {x: width, y: 0},
    {x: width, y: height},
    {x: 0, y: height}
  ]
  rect.clear()
  rect.lineStyle(2, 0xff0000)
  rect.drawDashedPolygon(rectPolygons, 0, 0, 0, 10, 5, 0)
  rect.x = pixiApp.view.width / 2 - width / 2
  rect.y = pixiApp.view.height / 2 - height / 2
}

function init (app) {
  pixiApp = app

  rect = new PIXI.Graphics()
  pixiApp.stage.addChild(rect)
  refreshRect()

  const dragArea = new PIXI.Graphics()
  dragArea.interactive = true
  dragArea.buttonMode = true
  dragArea.x = 0
  dragArea.y = 0

  dragArea.alpha = 0
  dragArea.beginFill(0x000000)
  dragArea.drawRect(0, 0, pixiApp.view.width, pixiApp.view.height)
  dragArea.endFill()
  dragArea.on('pointerdown', onDragStart)
    .on('pointerup', onDragEnd)
    .on('pointerupoutside', onDragEnd)
    .on('pointermove', onDragMove)

  pixiApp.stage.addChild(dragArea)
}

function onDragStart (event) {
  // this.dragging = true
  data = event.data
  startPosition = data.getLocalPosition(this.parent)

  startSize = {
    width: width,
    height: height
  }
}

function onDragEnd () {
  // this.dragging = false
  data = null
}

function onDragMove () {
  if (!data) return
  const newPosition = data.getLocalPosition(this.parent)

  let x = newPosition.x - startPosition.x
  let y = newPosition.y - startPosition.y

  width = Math.min(Math.max(startSize.width + x, 100), pixiApp.view.width)
  height = Math.min(Math.max(startSize.height + y, 100), pixiApp.view.height)

  refreshRect()
}

export default {
  init
}



