<template id="main">
  <v-ons-navigator swipeable 
    :page-stack="pageStack"
    @push-page="pageStack.push($event)"
    @replace-page="pageStack.pop();pageStack.push($event)"
    @canvas-page="goCanvas()"
    @home-page="goHome()"
  ></v-ons-navigator>
</template>

<script>
import HomePage from './HomePage.vue'

export default {
  name: 'navigation',
  data () {
    return {
      pageStack: [HomePage]
    }
  },
  methods: {
    goHome () {
      this.pageStack.pop()
      this.pageStack.push(HomePage)
    }
  },
  components: {
    HomePage
  }
}
</script>


