<template>
  <div :class="{ fadeout: !isLoading }">
    <lottie-view path="static/4-dots.json" />
  </div>
</template>

<script>
import LottieView from './LottieView.vue'

export default {
  name: 'splash',
  props: ['isLoading'],
  components: {
    LottieView
  }
}
</script>

<style scoped>
.fadeout {
  animation: fadeout 2s forwards;
}

@keyframes fadeout {
  to {
    opacity: 0;
    visibility: hidden;
  }
}
</style>


