<template>
  <div v-if="style" :style="style" ref="lavContainer" @click="$emit('click')" @mousedown="$emit('down')" @mouseup="$emit('up')" />
</template>

<script>
import lottie from 'lottie-web'
import axios from 'axios'

export default {
  props: {
    path: {
      required: true
    },
    speed: {
      type: Number,
      required: false,
      default: 1
    },
    width: {
      type: Number | String,
      required: false,
      default: -1
    },
    height: {
      type: Number | String,
      required: false,
      default: -1
    },
    loop: {
      type: Boolean,
      required: false,
      default: true
    },
    autoPlay: {
      type: Boolean,
      required: false,
      default: true
    },
    loopDelayMin: {
      type: Number,
      required: false,
      default: 0
    },
    loopDelayMax: {
      type: Number,
      required: false,
      default: 0
    }
  },
  data () {
    return {
      name: 'lottie-view',
      rendererSettings: {
        scaleMode: 'centerCrop',
        clearCanvas: true,
        progressiveLoad: false,
        hideOnTransparent: true
      },
      anim: null,
      style: null
    }
  },
  methods: {
    getRandomInt (min, max) {
      min = Math.ceil(min)
      max = Math.floor(max)
      return Math.floor(Math.random() * (max - min)) + min
    },
    executeLoop () {
      this.anim.play()
      setTimeout(() => {
        this.anim.stop()
        this.executeLoop()
      }, this.getRandomInt(this.loopDelayMin, this.loopDelayMax === 0 ? this.loopDelayMin : this.loopDelayMax))
    },
    sizeText (txt) {
      return isNaN(txt) ? txt : `${txt}px`
    },
    async init () {
      this.style = {
        width: (this.width !== -1) ? this.sizeText(this.width) : '100%',
        height: (this.height !== -1) ? this.sizeText(this.height) : '100%',
        overflow: 'hidden',
        margin: '0 auto'
      }
      let jsonData = await axios.get(this.path).then(response => response.data)

      if (this.anim) {
        this.anim.destroy()
      }

      this.anim = lottie.loadAnimation({
        container: this.$refs.lavContainer,
        renderer: 'svg',
        loop: this.loop,
        autoplay: this.autoPlay,
        animationData: jsonData,
        rendererSettings: this.rendererSettings
      })
      this.anim.addEventListener('complete', () => {
        this.$emit('complete', this.anim)
      })

      this.$emit('AnimControl', this.anim)

      this.anim.setSpeed(this.speed)

      if (this.loopDelayMin > 0) {
        this.anim.loop = false
        this.anim.autoplay = false
        this.executeLoop()
      }
    }
  },
  mounted () {
    this.init()
  }
}
</script>



