<template>
  <v-ons-page>
    <video v-show="!isLoading" class="camera-surface" ref="camera" autoplay></video>
    <canvas ref="pixiCanvas"></canvas>
    <lottie-view :class="['okBtn', isPhotoTaken ? 'fadein' : 'fadeout']"
      ref="btn" width="100px" height="100px"
      :speed="2" :loop=false :autoPlay="false"
      path="static/green-progress.json"
      @click="onClick" @complete="onComplete" />
  </v-ons-page>
</template>

<script>
import * as PIXI from 'pixi.js'
import Resizable from '../lib/resizable.js'
import LottieView from './LottieView.vue'

export default {
  name: 'CanvasPage',
  data () {
    return {
      app: null,
      isLoading: true,
      isPhotoTaken: false,
      imageCapture: null
    }
  },
  mounted () {
    navigator.mediaDevices.getUserMedia({audio: false, video: { facingMode: 'environment' }}).then(stream => {
      this.isLoading = false
      this.$refs.camera.srcObject = stream
      const track = stream.getVideoTracks()[0]
      this.imageCapture = new window.ImageCapture(track)
    }).catch(error => {
      this.isLoading = false
      console.error('Platform Webview does not support getUserMedia video.')
      console.error(error)
    })

    this.app = new PIXI.Application({
      backgroundAlpha: 0,
      antialias: true,
      view: this.$refs.pixiCanvas
    })
    // this.$el.appendChild(this.app.view)
    this.app.renderer.view.className = 'canvas-surface'
    this.app.renderer.view.style.position = 'fixed'
    this.app.renderer.view.style.width = '100%'
    this.app.renderer.view.style.height = '100%'
    this.app.renderer.view.style.zIndex = '10'
    this.app.renderer.autoResize = true
    this.app.renderer.resize(window.innerWidth, window.innerHeight)

    Resizable.init(this.app)
  },
  methods: {
    onClick () {
      console.log('onClick')
      this.isPhotoTaken = !this.isPhotoTaken
      if (this.isPhotoTaken) this.$refs.btn.anim.play()
      else this.$refs.btn.anim.stop()
    },
    onComplete () {
      console.log('onComplete')
      this.$refs.btn.anim.stop()
      this.isPhotoTaken = !this.isPhotoTaken
      this.takePhoto()
    },
    takePhoto () {
      if (this.isLoading) {
        console.log('camera is not ready')
        return
      }

      this.imageCapture.takePhoto()
        .then(blob => {
          console.log(blob)
          return createImageBitmap(blob)
        })
        .then(bitmap => {
          console.log(bitmap)
          new PIXI.ImageBitmapResource(bitmap).load().then(res => {
            const texture = PIXI.Texture.from(res)
            const img = new PIXI.Sprite(texture)
            img.x = 0
            img.y = 0
            img.scale.set(0.3, 0.3)
            this.app.stage.addChild(img)
          })
        }).catch(error => console.log(error))
    }
  },
  components: {
    LottieView
  }
}
</script>

<style scoped>
.camera-surface {
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.stage > canvas {
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 10;
}

.okBtn {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 20;
  opacity: 0;
}

.fadein {
  animation: fadein 400ms forwards;
}

.fadeout {
  animation: fadeout 400ms;
}

@keyframes fadein {
  to {
    opacity: 1;
  }
}

@keyframes fadeout {
  from { opacity: inherit;}
  to {
    opacity: 0;
  }
}
</style>


